import './TodoItem.css';
import {Reorder, useDragControls} from "framer-motion";
import { motion } from "framer-motion";


export const TodoItem = ({item}) => {
  const controls = useDragControls();

  return (
    <Reorder.Item
      key={item.id}
      value={item}
      className='TodoItem'
      dragListener={false}
      dragControls={controls}
    >
      {item.name}
      <BtnItem onPointerDown={(e) => controls.start(e)}/>
    </Reorder.Item>
  );
};

const BtnItem = (props) => {
  return (
    <div
      className="reorder-handle"
      {...props}
    ></div>
  )
};